<template>
  <v-container fluid rounded outlined class="lighten-5 findbar pa-2">
    <v-row no-gutters class="fill-height justify-space-around">
      <v-col cols="7" class="fill-height">
        <v-text-field
          dense
          single-line
          placeholder="Find..."
          height="15"
          hide-details
          autofocus
          v-model="value"
          @input="onInput"
          @keydown="onKeydown"
        />
      </v-col>

      <v-col fluid cols="5" class="d-flex align-center justify-space-around fill-height">
        <v-btn icon depressed small @click="onPrevClick">
          <v-icon>mdi-chevron-up</v-icon>
        </v-btn>
        <v-btn icon depressed small @click="onNextClick">
          <v-icon>mdi-chevron-down</v-icon>
        </v-btn>
        <toggled-button icon depressed small @click="onEntireWord">
          <v-icon>mdi-format-letter-matches</v-icon>
        </toggled-button>
        <toggled-button icon depressed small @click="onCaseSensitive">
          <v-icon>mdi-format-letter-case</v-icon>
        </toggled-button>
        <span>{{$store.getters.findNumber}}/{{$store.getters.findResults}}</span>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { internalEventBus } from "../eventbus";
import ToggledButton from "./ToggledButton";

export default {
  name: "Findbar",
  data() {
    return {
      value: null,
      caseSensitive: false,
      entireWord: false,
      highlightAll: false,
    };
  },
  components: {
    ToggledButton
  },
  methods: {
    dispatchEvent(type, findPrev) {
      internalEventBus.$emit("find", {
        type,
        query: this.value,
        phraseSearch: true,
        caseSensitive: this.caseSensitive,
        entireWord: this.entireWord,
        highlightAll: this.highlightAll,
        findPrevious: findPrev,
      });
    },
    onInput() {
      this.dispatchEvent("");
    },
    onKeydown(e) {
      switch (e.keyCode) {
        case 13:
          this.dispatchEvent("again", e.shiftKey);
          break;
        case 27:
          this.$store.commit("updateFindbarOn", false);
          break;
      }
    },
    onPrevClick() {
      this.dispatchEvent("again", true);
    },
    onNextClick() {
      this.dispatchEvent("again", false);
    },
    onEntireWord() {
      this.entireWord = !this.entireWord;
      this.dispatchEvent("");
    },
    onCaseSensitive() {
      this.caseSensitive = !this.caseSensitive;
      this.dispatchEvent("");
    }
    
    // Lack highlightAll, caseSensitive, and entireWord
    // But maybe try to restore text layer
  },
};
</script>

<style>
.findbar {
  position: fixed;
  top: 7%;
  left: 5%;
  z-index: 10000;
  width: 30%;
  height: 5%;
  background-color: white;
  border: 1px solid rgb(0, 0, 0);
}

.findbar span {
  font-size: 14px;
}

.v-text-field input {
  font-size: 15px;
}

.v-text-field {
  margin-right: 5px;
}
</style>