<template>
  <v-container fluid rounded outlined class="lighten-5 jumptobar pa-2">
    <v-row no-gutters class="fill-height justify-space-around">
      <v-col cols="10" class="fill-height">
        <v-text-field
          dense
          single-line
          placeholder="Jump to..."
          height="15"
          hide-details
          type="number"
          @keydown.enter="updatePageNumber"
          v-model="pageNumber"
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Jumptobar",
  data: () => ({
    pageNumber: null,
  }),
  methods: {
    updatePageNumber: function () {
      this.$store.dispatch("updatePageNumber", this.pageNumber);
    },
  },
};
</script>

<style>
.jumptobar {
  position: fixed;
  top: 7%;
  left: 15%;
  z-index: 10000;
  width: 7%;
  height: 5%;
  background-color: white;
  border: 1px solid rgb(0, 0, 0);
}

.jumptobar span {
  font-size: 14px;
}

.v-text-field input {
  font-size: 15px;
}
</style>