<template>
  <div id="viewerContainer" tabindex="0" @scroll="onScroll">
    <v-col id="viewer" class="pdfViewer" justify-space-between></v-col>
  </div>
</template>

<script>
export default {
  name: "PdfView",
  methods: {
    onScroll(evt) {
      this.$store.dispatch("onPdfViewScroll", evt);
    }
  }
}
</script>

<style>
#viewerContainer {
  position: absolute;
  overflow: auto;
  width: 100%;
  height: 100%;
}

.pdfViewer div {
  margin: auto;
  margin-bottom: 3%;
}
</style>