<template>
  <v-card>
    <v-toolbar dense dark fixed>
      <v-row class="justify-space-between align-center">
        <v-col class="d-flex justify-start toolbar-left" cols="4">
          <v-tooltip bottom open-delay="1500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on" @click="onClickSidebar">
                <v-icon>mdi-page-layout-sidebar-left</v-icon>
              </v-btn>
            </template>
            <span>Sidebar</span>
          </v-tooltip>

          <v-tooltip bottom open-delay="1500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on" @click="onClickFind">
                <v-icon>mdi-magnify</v-icon>
              </v-btn>
            </template>
            <span>Find</span>
          </v-tooltip>

          <v-tooltip bottom open-delay="1500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on" :disabled="$store.getters.prevBtnDisabled" @click="$store.dispatch('prevPage')">
                <v-icon>mdi-chevron-left</v-icon>
              </v-btn>
            </template>
            <span>Previous Page</span>
          </v-tooltip>

          <v-tooltip bottom open-delay="1500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on" :disabled="$store.getters.nextBtnDisabled" @click="$store.dispatch('nextPage')">
                <v-icon>mdi-chevron-right</v-icon>
              </v-btn>
            </template>
            <span>Next Page</span>
          </v-tooltip>

          <v-tooltip bottom open-delay="1500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon v-bind="attrs" v-on="on" @click="onClickJump">
                <v-icon>mdi-debug-step-over</v-icon>
              </v-btn>
            </template>
            <span>Jump to</span>
          </v-tooltip>
        </v-col>

        <v-col class="d-flex align-center toolbar-middle" cols="4">
          <v-row class="align-center justify-center">
            <v-col class="d-flex align-center" cols="3">
              <span>{{$store.getters.pageNumber}} of {{$store.getters.numPages}}</span>
            </v-col>

            <v-col class="d-flex" cols="3">
              <v-btn icon>
                <v-icon>mdi-plus</v-icon>
              </v-btn>
              <v-btn icon>
                <v-icon>mdi-minus</v-icon>
              </v-btn>
            </v-col>

            <v-col class="d-flex" cols="5">
              <v-select :items="ratios" dense hide-details single-line label="Zoom"></v-select>
            </v-col>
          </v-row>
        </v-col>

        <v-col cols="4" class="d-flex justify-end toolbar-right">
          <v-btn icon>
            <v-icon>mdi-fullscreen</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-printer</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-download</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-bookmark</v-icon>
          </v-btn>
          <v-btn icon>
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-toolbar>
  </v-card>
</template>

<script>
export default {
  name: "Toolbar",
  data() {
    return {
      ratios: [
        "Automatic Zoom",
        "Actual Size",
        "Page Fit",
        "Page Width",
        "50%",
        "75%",
        "100%",
        "125%",
        "150%",
        "200%",
        "300%",
        "400%",
      ],
      prevBtnDisabled: true,
      nextBtnDisabled: false,
    };
  },
  methods: {
    onClickFind(event) {
      this.$store.dispatch("toggleElement", "findbarOn");
    },
    onClickJump(event) {
      this.$store.dispatch("toggleElement", "jumpbarOn");
    },
    onClickSidebar(event) {
      this.$store.dispatch("toggleElement", "sidebarOn");
    },
  },
};
</script>