<template>
  <v-app>
    <toolbar></toolbar>
    <findbar v-if="$store.getters.findbarOn"></findbar>
    <jumptobar v-if="$store.getters.jumpbarOn"></jumptobar>
    <sidebar v-model="$store.getters.sidebarOn"></sidebar>
    <v-main>
      <pdf-view></pdf-view>
    </v-main>
  </v-app>
</template>

<script>
import Toolbar from "./components/Toolbar";
import Findbar from "./components/Findbar";
import Jumptobar from "./components/Jumptobar";
import Sidebar from "./components/Sidebar";
import PdfView from "./components/PdfView";
import { webViewerLoad } from './lib/viewer';

export default {
  name: "App",
  data() {
    return {
      findbarOn: false,
      jumpbarOn: false,
      sidebarOn: false,
    };
  },
  components: {
    Toolbar,
    Findbar,
    Jumptobar,
    Sidebar,
    PdfView
  },
  mounted() {
    webViewerLoad();
  },
  methods: {
    toggleElement(elem) {
      var curr = this[elem];
      this.toggleAllOff();
      this[elem] = !curr;
    },
    toggleAllOff() {
      this.findbarOn = false;
      this.jumpbarOn = false;
      this.sidebarOn = false;
    }
  },
};
</script>

