<template>
  <v-btn v-bind="$props" :color="bgColor" @click="clicked">
    <slot></slot>
  </v-btn>
</template>

<script>
export default {
  props: ['icon', 'depressed', 'small'],
  data: function () {
    return {
      bgColor: null,
      toggled: false,
    }
  },
  methods: {
    clicked() {
      this.toggled = !this.toggled;
      if (this.toggled) this.bgColor = "primary";
      else this.bgColor = null;
      this.$emit('click');
    }
  },
}
</script>
