<template>
  <v-navigation-drawer v-model="sidebarOn" app temporary dark hide-overlay class="sidebar">
    <v-row class="tabs d-flex">
      <v-tabs fixed-tabs background-color="indigo" dark width="100" v-model="tab">
        <v-tab>
          <v-icon>mdi-grid-large</v-icon>
        </v-tab>
        <v-tab>
          <v-icon>mdi-format-list-text</v-icon>
        </v-tab>
        <v-tab>
          <v-icon>mdi-attachment</v-icon>
        </v-tab>
      </v-tabs>
    </v-row>
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <thumbnail-view></thumbnail-view>
      </v-tab-item>
      <v-tab-item>
        <v-img src="/static/largepreview.png" contain></v-img>
      </v-tab-item>
    </v-tabs-items>
  </v-navigation-drawer>
</template>

<script>
import ThumbnailView from './ThumbnailView';
export default {
  name: "Sidebar",
  props: ["value"],
  data() {
    return {
      items: [{ title: "Home", icon: "mdi-fullscreen" }],
      tab: null,
    };
  },
  components: {
    ThumbnailView
  },
  computed: {
    sidebarOn: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      },
    },
  },
};
</script>

<style>
.sidebar {
  top: 6.3% !important;
}
</style>